@media only screen and (max-width: 1023px) { .ico-bars { width: 350px; margin: 0 auto; } }

.ico-bar { display: inline-block; margin-bottom: 10px; background-color: #24bc52; padding-left: 10px; border-radius: 2px; font-family: "MuseoSansCyrl-Medium", Arial, sans-serif; font-size: 24px; color: #FFF; }
@media only screen and (max-width: 767px) { .ico-bar { font-size: 22px; } }

[data-bar='1'] { padding-right: 5px; }

[data-bar='2'] { padding-right: 10px; }

[data-bar='3'] { padding-right: 16px; }

[data-bar='4'] { padding-right: 21px; }

[data-bar='5'] { padding-right: 27px; }

.ico-progress__line { position: relative; width: 390px; height: 8px; margin: 25px 0 30px; background-color: #69717B; }
@media only screen and (max-width: 767px) { .ico-progress__line { width: 100%; } }
.ico-progress__line span:first-child { position: absolute; left: 0; top: 0; height: 100%; width: 83%; background-color: #005E30; z-index: 2; }
.ico-progress__line span:last-child { position: absolute; left: 0; top: 0; height: 100%; width: 95%; background-color: #24BC52; z-index: 1; }
.ico-progress__item { margin-bottom: 5px; font-size: 24px; font-family: "MuseoSansCyrl-Light", Arial, sans-serif; color: #000; }
.ico-progress__item span { font-size: 30px; font-family: "MuseoSansCyrl-Bold", Arial, sans-serif; }
.ico-progress__item span.is-darkGreen { color: #005E30; }
.ico-progress__item span.is-green { color: #24BC52; }
.ico-progress__item span.lightGray { color: #69717B; }
@media only screen and (max-width: 767px) { .ico-progress__item { font-size: 18px; }
  .ico-progress__item span { font-size: 24px; } }

.b-plan { text-align: center; }
.b-plan > .text { margin-bottom: 40px; }
.b-plan__title { margin-bottom: 15px; font-size: 30px; color: #25BC53; text-transform: uppercase; font-family: "MuseoSansCyrl-Light", Arial, sans-serif; }
@media only screen and (max-width: 767px) { .b-plan__title { font-size: 26px; } }
.b-plan__img { width: 100%; }
.b-plan__figure { margin-bottom: 20px; }
.b-plan__figure + .b-plan__figure { margin-top: 30px; margin-bottom: 50px; }
.b-plan__footer { font-size: 18px; color: #000; font-family: "MuseoSansCyrl-Light", Arial, sans-serif; }

.members { margin: 30px 0; font-size: 0; }
@media only screen and (max-width: 1023px) { .members { margin: 30px 0 -20px; } }

.member { display: inline-block; vertical-align: top; }
.member__col { display: inline-block; width: 25%; text-align: center; }
@media only screen and (max-width: 1023px) { .member__col { width: 50%; margin-bottom: 60px; } }
@media only screen and (max-width: 767px) { .member__col { width: 100%; margin-bottom: 45px; } }
.member__photo { width: 200px; height: 200px; border-radius: 50%; margin: 0 auto 25px; overflow: hidden; }
.member__photo img { width: 100%; }
@media only screen and (max-width: 767px) { .member__photo { width: 150px; height: 150px; } }
.member__name { font-size: 30px; line-height: 1; color: #005E30; font-family: "MuseoSansCyrl-Light", Arial, sans-serif; }
@media only screen and (max-width: 767px) { .member__name { font-size: 26px; } }
.member__position { margin-bottom: 10px; font-size: 20px; color: #444e5a; font-family: "MuseoSansCyrl-Light", Arial, sans-serif; }
.member__mail { transition: opacity 0.15s; }
.member__mail .icon-mail { background-image: url("../img/icons.png"); width: 34px; height: 27px; background-position: -147px -52px; }
.member__mail:hover { opacity: 0.7; }

.faq-list { margin: 30px 0; }

.faq:not(:last-child) { margin-bottom: 15px; }
.faq__answer { font-family: "MuseoSansCyrl-Light", Arial, sans-serif; }
.faq__question { font-size: 24px; color: #24BC52; font-family: "MuseoSansCyrl-Bold", Arial, sans-serif; }
@media only screen and (max-width: 767px) { .faq__question { font-size: 22px; } }
.faq__question:before { content: "Q: "; color: currentColor; font-size: 24px; }
.faq__answer { font-size: 18px; color: #000; }
.faq a { color: #24BC52; }


